html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
:root {
  font-size: 62.5%;
}
body {
  background-color: #555555;
  color: antiquewhite;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
#appDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrapper { 
    background:#111;
    margin: auto; 
    text-align: center; 
    position: relative;
    margin-bottom: 20px !important;
    width: 100vw;
    padding: 1rem;
}
.scrolls { 
    overflow-x: scroll;
    overflow-y: hidden;
    height: 75vh;
    white-space: nowrap;
} 
img.scroll-pic { 
    border: 0.03rem solid #444;
    border-radius: 0.2rem;
    margin-left: 0.7rem;
    margin-right: 0.7rem; 
    height: 60vh;
    cursor: pointer;
    display: inline-block;
    *display: inline;
    *zoom: 1;
    vertical-align: middle;
}
img.scroll-pic.active {
    height: 75vh;
}
